"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { authClient } from "@/app/lib/auth-client";
import { useSession } from "@/app/lib/auth-client";
import { handleDeletedUser } from "@/actions/postReport";
import { useRouter } from "next/navigation";
import {
  Dialog,
  DialogContent,
  DialogTrigger,
  DialogTitle,
} from "@/components/ui/dialog";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { toast } from "sonner";

const settingsSchema = z.object({
  username: z.string().min(4, "Username must be at least 4 characters").max(30),
  currentPassword: z.string().min(6, "Current password is required"),
  newPassword: z
    .string()
    .min(6, "New password must be at least 6 characters")
    .refine((newPassword, ctx) => {
      if (newPassword === ctx.parent.currentPassword) {
        return false;
      }
      return true;
    }, "New password cannot be the same as the current password"),
  confirmDeletePassword: z
    .string()
    .min(6, "Password is required to delete account"),
});

export default function SettingsModal() {
  const form = useForm<z.infer<typeof settingsSchema>>({
    resolver: zodResolver(settingsSchema),
    defaultValues: {
      username: "",
      currentPassword: "",
      newPassword: "",
      confirmDeletePassword: "",
    },
  });

  const router = useRouter();
  const [isOpen, setIsOpen] = useState(false);

  const { data: session } = useSession();

  const handleUsernameChange = async () => {
    try {
      if (!session?.user?.id) {
        toast.error("You must be logged in to change your username");
        return;
      }

      await authClient.updateUser(
        { username },
        {
          onSuccess: () => {
            toast.success("Username changed successfully!");
            setUsername("");
            router.refresh();
          },
          onError: () => {
            toast.error("Error changing username");
          },
        }
      );
    } catch (error) {
      console.log(error);
      toast.error("Error changing username");
    }
  };

  const handlePasswordChange = async () => {
    try {
      if (!session?.user?.id) {
        toast.error("You must be logged in to change your password");
        return;
      }

      if (!newPassword || !currentPassword) {
        console.log("Both new password and current password are required");
        toast.error("Both new password and current password are required");
        return;
      }

      if (newPassword === currentPassword) {
        console.log("New password cannot be the same as the current password");
        toast.error("New password cannot be the same as the current password");
        return;
      }

      await authClient.changePassword(
        {
          newPassword,
          currentPassword,
          revokeOtherSessions: true,
        },
        {
          onSuccess: () => {
            toast.success("Password changed successfully!");
            setCurrentPassword("");
            setNewPassword("");
          },
        }
      );
    } catch (error) {
      console.log(error);
      toast.error("Error changing password");
    }
  };

  const handleUserDelete = async (password: string) => {
    try {
      if (!password) {
        console.log("Password is required to delete account");
        toast.error("Password is required to delete account");
        return;
      }

      await handleDeletedUser(session?.user.id);

      await authClient.deleteUser(
        {
          password,
        },
        {
          onSuccess: () => {
            router.refresh();
          },
        }
      );
    } catch (error) {
      console.log(error);
      toast.error("Error deleting account");
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      {/* Trigger Button */}
      <DialogTrigger asChild>
        <Button className="z-50 bg-white text-black outline hover:bg-gray-100 hover:cursor-pointer">
          Settings
        </Button>
      </DialogTrigger>

      {/* The Modal */}
      <DialogTitle className="sr-only">User Settings</DialogTitle>
      <DialogContent className="bg-white p-6 rounded-lg shadow-lg max-w-md mx-auto">
        <div className="flex justify-between items-center">
          <h2 className="text-lg font-bold">User Settings</h2>
        </div>

        <div className="mt-4">
          <Label>Update Username</Label>
          <Input
            type="username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            placeholder="Enter your new username"
          />
        </div>

        <div className="flex justify-center">
          <Button
            onClick={() => handleUsernameChange()}
            className="w-36 h-11 mt-4 text-black border border-black bg-white hover:bg-gray-100 hover:cursor-pointer"
          >
            Update Username
          </Button>
        </div>

        <h2 className="text-lg font-bold mt-4">Change Password</h2>
        <div className="mt-4">
          <Label>Current Password</Label>
          <Input
            type="password"
            value={currentPassword}
            onChange={(e) => setCurrentPassword(e.target.value)}
            placeholder="Enter your current password"
          />
        </div>
        <div className="mt-4">
          <Label>New Password</Label>
          <Input
            type="password"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            placeholder="Enter your new password"
          />
        </div>
        <div className="flex justify-center">
          <Button
            onClick={() => handlePasswordChange()}
            className="w-36 h-11 mt-4 text-black border border-black bg-white hover:bg-gray-100 hover:cursor-pointer"
          >
            Update Password
          </Button>
        </div>

        <div className="flex justify-center">
          <AlertDialog>
            <AlertDialogTrigger asChild>
              <Button
                variant={"destructive"}
                className="bg-red-600 shadow-md hover:bg-red-500 hover:cursor-pointer text-white border border-black mt-4"
              >
                Delete Account
              </Button>
            </AlertDialogTrigger>
            <AlertDialogContent>
              <AlertDialogHeader>
                <AlertDialogTitle>
                  Are you sure you want to delete your account?
                </AlertDialogTitle>
                <AlertDialogDescription>
                  This action cannot be undone. This will permanently delete
                  your account and remove your data from our servers.
                </AlertDialogDescription>
                <div className="">
                  <Label>Enter Password to Confirm</Label>
                  <Input
                    type="password"
                    value={passwordDelete}
                    onChange={(e) => setPasswordDelete(e.target.value)}
                    placeholder="Enter your password"
                  />
                </div>
              </AlertDialogHeader>
              <AlertDialogFooter>
                <AlertDialogCancel>Cancel</AlertDialogCancel>
                <AlertDialogAction
                  onClick={() => handleUserDelete(passwordDelete)}
                  className="bg-red-500 text-white hover:bg-red-600"
                >
                  Delete Account
                </AlertDialogAction>
              </AlertDialogFooter>
            </AlertDialogContent>
          </AlertDialog>
        </div>
      </DialogContent>
    </Dialog>
  );
}
